window.FN = window.FN || {};
window.FN = {};

FN.Controllers = angular.module('FNApp.FNControllers');

FN.Controllers.controller('FNFNshopnameController', [
    '$http','$scope',
    function($http,$scope) {
  
		$scope.Bname = undefined;
$http({
 
                method: 'GET',
 
                url: $scope.baseURL+'/shopname/shop_name'
 
            }).success(function (data) {
 
                console.log(data);
 
                $scope.Bname = data;
 
            });

    }]
);