	    		  					$('.results-table-ilb ul:nth-of-type('+(i+1)+') > li:nth-child(2)').html(this.main);
	    		  					$('.results-table-ilb ul:nth-of-type('+(i+1)+') > li:nth-child(3)').html(this.bonus);
